.posts_form {
  display: flex;
  flex-direction: column;
  margin-top: 24px;
}

.posts_form h2 {
  margin-bottom: 16px;
}

.posts_form form {
  display: flex;
  flex-direction: column;
}

.posts_form input {
  width: 80%;
  border: none;
  background: #F8F9FA;
  padding: 20px;
  color: black;
  margin-bottom: 12px;
  border: 1px solid black;
  border-radius: 6px;
}

.posts_form button {
  color: white;
  background: #1D8BD8;
  width: fit-content;
  padding: 8px 12px;
  border-radius: 6px;
  cursor: pointer;
  border: none;
  transition: background 0.3s ease-in-out;
}

.posts_form button:hover {
  background: black;
}

.posts {
  display: flex;
  flex-direction: column;
  margin-top: 42px;
}

.error {
  border: 1px solid red !important;
}

.errorMessage {
  width: fit-content;
  color: red;
  margin: 5px 0 15px;
}